

#role {
    width: 100%;
    height: 100%;
    background: #fff;
    box-sizing: border-box;
    padding: 20px;
    color: #000;
}
#role>h2 {
    text-align: center;
    font-size: 30px;
    line-height: 80px;
}
#role>table {
    width: 100%;
    height: auto;
}
#role>table td>span{

    margin: 0 3px;
    color: red;
    cursor: pointer;
}   

.text {
    display: block;
    margin: 30px 0;
    width: 100%;
    height: 200px;
    font-size: 20px
}
.btn{
    text-align: center;
}
.inp {
    width: 100%;
    height: 1rem;
    font-size: 16px;
}
